// IMPORTS
// Material UI
import Slide from '@material-ui/core/Slide'
import withStyles from '@material-ui/core/styles/withStyles'

// Externals imports (Dependencies)
import PropTypes from 'prop-types'
import React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'

// Internal imports
import List from './List'
import styles from '../../../../resources/theme/distributionCenters'
import EnhancedComponent from '../../../components/EnhancedComponent'
import { fetchFleetsPerDistributionCenters, saveFleetsPerDistributionCenters } from '../../../fleet/FleetActions'

function Transition(props) {
  return <Slide direction="down" {...props} />
}

class DistributionCentersFleets extends EnhancedComponent {
  state = {
    dirty: false,
    fields: {},
  }

  componentDidMount() {
    this.props.fetchFleetsPerDistributionCenters(this.props.match.params.id)
  }

  onSubmit = values => {
    this.props
      .saveFleetsPerDistributionCenters(values, this.props.match.params.id)
      .then(data => {
        if (data && data.code === 200) {
          this.props.history.push('/distributioncenters')
        }
      })
  }

  onClose = () => {
    this.props.history.push('/distributioncenters')
  }

  render() {
    const { classes, items } = this.props
    return (
      <List
        data={items}
        classes={classes}
        onClose={this.onClose}
        onSubmit={this.onSubmit}
      />
    )
  }
}

DistributionCentersFleets.propTypes = {
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({
  items: state.fleets.items_distributionCenter,
})

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
        fetchFleetsPerDistributionCenters,
        saveFleetsPerDistributionCenters,
    },
    dispatch
  )

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(DistributionCentersFleets))
